<template>
  <div class="cy_win">
     <div class="cy_win_con">
         <div class="line"></div>
        <!-- 示例一 -->
        <div id="myWin1" class="myWin">
            <div class="item">
                <p>中国人</p>
                <p>五千年的风和雨啊  藏了多少梦</p>
                <p>黄色的脸 黑色的眼  不变是笑容</p>
                <p>八千里山川河岳  像是一首歌</p>
                <p>不论你来自何方  将去向何处</p>
                <p>一样的泪 一样的痛  曾经的苦难</p>
                <p>我们留在心中  一样的血 一样的种</p>
                <p>未来还有梦  我们一起开拓</p>
                <p>手牵着手 不分你我  昂首向前走</p>
                <p>让世界知道  我们都是中国人</p>
                <p>五千年的风和雨啊  藏了多少梦</p>
                <p>黄色的脸 黑色的眼  不变是笑容</p>
                <p>八千里山川河岳  像是一首歌</p>
                <p>不论你来自何方  将去向何处</p>
            </div>
        </div>
<div class="codeReadonlyModule" style="height:180px">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;script type="text/javascript"&gt;
import sliderScroll from "cy_static/sliderScroll";
sliderScroll({
    elem: "#myWin2",
    content: ".item",
    hasMask: true
})
&lt;/script&gt;
</xmp>
</div>
        <!-- 示例三 -->
        <div class="line"></div>
        <div id="myWin3" class="myWin">
            <div class="item">
                <p>中国人</p>
                <p>五千年的风和雨啊  藏了多少梦</p>
                <p>黄色的脸 黑色的眼  不变是笑容</p>
                <p>八千里山川河岳  像是一首歌</p>
                <p>不论你来自何方  将去向何处</p>
                <p>一样的泪 一样的痛  曾经的苦难</p>
                <p>我们留在心中  一样的血 一样的种</p>
                <p>未来还有梦  我们一起开拓</p>
                <p>手牵着手 不分你我  昂首向前走</p>
                <p>让世界知道  我们都是中国人</p>
                <p>五千年的风和雨啊  藏了多少梦</p>
                <p>黄色的脸 黑色的眼  不变是笑容</p>
                <p>八千里山川河岳  像是一首歌</p>
                <p>不论你来自何方  将去向何处</p>
            </div>
        </div>
<div class="codeReadonlyModule" style="height:210px">
<codemirror ref="myEditor2" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal2" style="display:none">
&lt;script type="text/javascript"&gt;
import sliderScroll from "cy_static/sliderScroll";
sliderScroll({
    elem: "#myWin2",
    content: ".item",
    pullDown: {},
    pushDown: {}
})
&lt;/script&gt;
</xmp>
</div>
        <div class="line"></div>
     </div>       
  </div>
</template>

<script>
  import {trim} from "../../../assets/js/codeStyle";
  import sliderScroll from "cy_static/sliderScroll";
  export default {
    name: 'example',
    data () {
      return {
        outWin : null,// 窗口
        cmOptions:{
              readOnly:"true",
              mode : "text/html",  // 模式
              indentUnit : 2,  // 缩进单位，默认2
              smartIndent : true,  // 是否智能缩进
              tabSize : 4,  // Tab缩进，默认4
              showCursorWhenSelecting : true

        }
      }
    },
    mounted(){
        this.initOutWin();
        this.example1();
        this.example2();

        var m = this;
        ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
            var dom, valDom, index = 0, getDom = function() {
                index++;
                dom = m.$refs[element + index];
                valDom = m.$refs[element + "Val" + index];
            };
            getDom();
            while (dom) {
                dom.codemirror.setValue(trim(valDom.innerHTML));
                getDom();
            }
        });

    },
    methods: {         
        initOutWin : function () {
            var m = this, cy_win = document.querySelector('.cy_win');   
            if (m.outWin) return;             
            m.outWin = sliderScroll({
                elem: cy_win,// 窗口元素
                content: ".cy_win_con",
                isTopEffect: false,
                isBotEffect: false,
                isLockfull: true,
                pushDown: {}// 底部加载
            });
            m.outWin.isEndPull(true);
            m.outWin.isEndPush(true);
        },
        example1 () {
            sliderScroll({
                elem: "#myWin1",
                content: ".item",
                hasMask: true,
                hasHoveScrollbar: true,
                onStart: function(height, event){
                    event.preventDefault();
                }
            })
        },
        example2 () {
            var ins3 = sliderScroll({
                elem: "#myWin3",
                content: ".item",
                hasHoveScrollbar: true,
                pullDown: {},
                pushDown: {},
                onStart: function(height, event){
                    event.preventDefault();
                }
            })
            ins3.isEndPull(true);
            ins3.isEndPush(true);
        }
    },
    activated () {
        this.outWin.setActivated(true);
    },
    deactivated(){
        this.outWin.setActivated(false);
    },
    beforeDestroy() {
        this.outWin.destroy();
    }
  }
</script> 

  <style scoped>
  .codeModule {padding: 5px 8px;}
  .myWin{cursor: n-resize;position:relative;overflow:hidden;height: 150px;background-color:#fff;border-top: 1px solid #f1f4f8;border-bottom: 1px solid #f1f4f8;}
  .myWin .item{background-color:#d9d9d9;min-height: 150px;line-height: 1.5;text-align: center;}
 

  </style>


